body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to top, #2c3e50, #3498db);
  
}
@font-face {
  font-family: 'Raleway';
  src: url("https://fonts.googleapis.com/css?family=Raleway:200,400,600");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  color: aliceblue;
  font-size: 100%;
}
  @media only screen and (max-width: 900px) {
    * {font-size: 90%};
  }
  @media only screen and (max-width: 600px) {
      * {font-size: 80%};
  }

.box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  width: 100vw;
  height: 100vh;
}
.heading {
  font-size: 6rem;
  font-weight: 200;
}
/* ACTIONS */

.red {
  color: rgb(255, 51, 51);
  border-bottom: 1px solid rgb(252, 77, 77);
}
.hidden {
  display: none;
}


.fadeOut {
  opacity: 0;
  transition: opacity 500ms ease-out;
}


/* MAIN VIEW */

.heading-secondary {
    font-size: 1.5rem;
    font-weight: 100;
    margin-bottom: 3rem;
  }
  .input {
    border: none;
    border-bottom: 1px solid white;
    width: 10rem;
    background-color: transparent;
    text-align: center;
    padding-bottom: .5rem;
    font-size: 1rem;
  }
  .input:focus {
    outline: none;
  }
  .input::-webkit-input-placeholder {
    color: aliceblue;
    text-align: center;
    font-size: 1rem;
  }
  .input::-ms-input-placeholder {
    color: aliceblue;
    text-align: center;
    font-size: 1rem;
  }
  .input::placeholder {
    color: aliceblue;
    text-align: center;
    font-size: 1rem;
  }
  
/* BUTTON */

.button {
    border-radius: 8px;
    border: 2px solid rgb(247, 172, 33);
    background-color: transparent;
    color: white;
    padding: .5rem 1.5rem;
    margin: 0 auto;
    margin-top: 3rem;
    font-size: .8rem;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  .button:focus {
    outline: none;
  }
  .button:hover {
    background-color: rgb(247, 172, 33);
  }
/* RESULTS VIEW */
.result-card-box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    max-width: 80vw;
    margin: 0 auto;
    transition: all .3s;
    
}
.temperature-large_container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    margin: 0 auto;
    margin-bottom: 2rem;
    width: 40%;
    height: 100px;
  }
  .temperature-img {
    width: 70px;
    height: 50px;
    margin-left: 2rem;
  }
  .temperature-large {
    font-size: 5rem;
    font-weight: 300;
    
  }
  .info {
    text-align: center;
    font-size: 1rem;
    font-weight: 100;
    margin-bottom: .5rem;
  }
  .heading-results {
    font-size: 4rem;
    margin-bottom: 2rem;
    margin-top: 0;
    text-align: center;
    font-weight: 200;
  }
  .togggle-days_container {

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 50%;
    height: 30px;
    margin: 0 auto;
    margin-top: 2rem;
  }
  .toggle-days {
    font-size: 1rem;
    width: 100%;
    text-align: center;
    padding: .5rem 1rem;
    border-radius: 5px;
    -webkit-align-self: center;
            align-self: center;
  }
  .toggle-days:hover {
    background-color: cornflowerblue;
    cursor: pointer;
  }
  .temps_container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 30vw;
  }
  .left-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
  }

  .margin-top {
    margin-top: 6rem;
  }
  .hidden {
    /* display: none; */
  }
  .temperature-img-small {
    width: 60px;
    height: 60px;
  }
  .temp-small-box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 1rem 1rem;
    background-color: rgba(62, 240, 240, 0.055);
    border-radius: 1rem;
    padding: 1rem;
    max-height: 80%;
  }
  .temp-small-item {
    font-size: .8rem;
    margin: .4rem;
  }
  @media only screen and (max-width: 740px) {
    .temp-small-box {
      padding: 0;
      -webkit-flex-direction: row;
              flex-direction: row;
    }
}
